<template xmlns:v-bind="http://www.w3.org/1999/xhtml">
  <div class="container">
    <div class="content">
      <h1 class="name">{{name}}</h1>
      <p class="from">{{from}}</p>
      <p class="stack">{{stack}}</p>
      <span v-for="item in links">
        <a class="link" :href="item.link" target="_blank" :title="item.label">
          <i v-bind:class="`iconfont ${item.icon}`"></i>
          <span>{{item.label}}</span>
        </a>
      </span>
    </div>
    <div class="footer">
      <span class="copyright">©KevinOfNeu</span>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Index',
    data () {
      return {
        name: 'KevinOfNeu',
        from: '@Shanghai',
        stack: 'Android Developer/Java/RN,Weex/Hybrid',
        links: [
          {label: 'LinkedIn', link: 'https://www.linkedin.com/in/hongju-wong-a7b640ab/', icon: 'icon-linkedin'},
          {label: 'Github', link: 'https://github.com/KevinOfNeu', icon: 'icon-github'},
          {label: 'Blog', link: 'https://blog.0xff000000.com', icon: 'icon-blog'},
          {label: 'Mail', link: 'mailto:kevinofneu#gmail.com', icon: 'icon-email'},
        ]
      }
    }
  }
</script>

<style scoped>
  .content {
    position: absolute;
    text-align: center;
    width: 500px;
    margin-left: -250px;
    margin-top: -250px;
    left: 50%;
    top: 50%;
    font-family: BlinkMacSystemFont, "Microsoft Yahei UI Light", "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
  }

  .name {
    color: #666;
  }

  .from {
    color: #666;
  }

  .stack {
    color: #999;
    font-size: 15px;
    margin-top: 30px;
    margin-bottom: 60px;
  }

  .link {
    color: #666;
    margin: 15px;
    font-size: 15px;
    text-decoration: none;
  }

  .iconfont {
    margin-left: 5px;
    margin-right: 5px;
  }

  .footer {
    position: absolute;
    text-align: center;
    width: 320px;
    margin-left: -160px;
    bottom: 60px;
    left: 50%;
  }

  .copyright {
    color: #666;
    font-size: 12px;
    font-family: BlinkMacSystemFont, "Microsoft Yahei UI Light", "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
    text-decoration: none;
  }
</style>



